// components/Board.tsx
import { Board, Position } from '@/types/types';
import { Circle, Square } from 'lucide';

interface BoardProps {
  board: Board;
  onCellClick: (row: number, col: number) => void;
  selectedPiece: Position | null;
}

const BoardComponent: React.FC<BoardProps> = ({ board, onCellClick, selectedPiece }) => {
  return (
    <div className="grid grid-cols-5 gap-2 w-96 h-96 mx-auto">
      {board.map((row, rowIndex) =>
        row.map((cell, colIndex) => (
          <div
            key={`${rowIndex}-${colIndex}`}
            className={`flex items-center justify-center w-16 h-16 rounded-lg cursor-pointer transition-all duration-200 ${
              cell === 1
                ? 'bg-black hover:bg-gray-900'
                : cell === 2
                ? 'bg-white hover:bg-gray-200'
                : 'bg-gray-300 hover:bg-gray-400'
            } ${
              selectedPiece &&
              selectedPiece[0] === rowIndex &&
              selectedPiece[1] === colIndex
                ? 'border-4 border-blue-500'
                : ''
            }`}
            onClick={() => onCellClick(rowIndex, colIndex)}
          >
            {cell === 1 && <Circle className="text-white text-3xl" />}
            {cell === 2 && <Square className="text-black text-3xl" />}
          </div>
        ))
      )}
    </div>
  );
};

export default BoardComponent;